.page__footer {
  margin: 0;
  margin-top: auto; }

.wrapper {
  min-width: 1024px;
  width: 60.4vw;
  padding: 0;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .wrapper {
      min-width: 640px;
      width: 87vw; } }
  @media (max-width: 767px) {
    .wrapper {
      min-width: 270px;
      width: 84vw; } }

.header {
  position: fixed;
  width: 100vw;
  min-height: 120px;
  padding: 30px 0;
  margin: 0;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  box-sizing: border-box; }
  @media (max-width: 1023px) {
    .header {
      position: static;
      min-height: 100px;
      padding: 20px 0; } }
  @media (max-width: 767px) {
    .header {
      min-height: 70px;
      padding: 15px 0; } }

.header__wrapper {
  display: flex;
  justify-content: space-between;
  padding-right: 60px; }
  @media (max-width: 1023px) {
    .header__wrapper {
      padding-right: 10px;
      box-sizing: border-box; } }
  @media (max-width: 767px) {
    .header__wrapper {
      padding: 0; } }

.header__button {
  padding: 0;
  padding-top: 5px;
  margin: 0; }
  @media (max-width: 1023px) {
    .header__button {
      display: none; } }

.header__button button {
  width: 160px;
  height: 40px;
  margin: 0;
  background: none;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
  box-sizing: border-box;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #f2f5f7;
  cursor: pointer; }
  .header__button button:hover {
    opacity: 0.7; }
  .header__button button:active {
    opacity: 0.3; }

.logo {
  min-height: 55px;
  padding: 0;
  margin: 0; }
  @media (max-width: 767px) {
    .logo {
      min-height: 35px; } }

.logo a {
  display: flex;
  padding: 0;
  margin: 0;
  cursor: pointer; }
  .logo a:hover {
    opacity: 0.7; }
  .logo a:active {
    opacity: 0.3; }

.logo__icon {
  width: 55px;
  height: 55px; }
  @media (max-width: 767px) {
    .logo__icon {
      width: 35px;
      height: 35px; } }

.logo__text {
  width: 145px;
  height: 45px;
  padding-top: 3px;
  margin-left: 15px; }
  @media (max-width: 767px) {
    .logo__text {
      display: none; } }

.logo svg {
  width: 100%;
  height: 100%; }

.nav {
  min-width: 480px;
  padding: 0;
  padding-top: 15px;
  margin: 0;
  box-sizing: border-box; }
  @media (max-width: 1023px) {
    .nav {
      min-width: 100px;
      padding-top: 10px; } }
  @media (max-width: 767px) {
    .nav {
      padding-top: 5px; } }

.nav ul {
  display: flex;
  justify-content: space-between;
  padding: 0 5px;
  margin: 0;
  list-style: none; }
  @media (max-width: 1023px) {
    .nav ul {
      padding: 0; } }

.nav li {
  padding: 0;
  margin: 0; }

.nav__item {
  padding: 0;
  margin: 0; }

.nav__item a {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0; }

.nav__item svg {
  margin-right: 15px; }

.nav__item p {
  padding: 0;
  margin: 0;
  line-height: 18px;
  color: #f2f5f7; }
  @media (max-width: 1023px) {
    .nav__item p {
      display: none; } }

