.logo {
  min-height: 55px;
  padding: 0;
  margin: 0;

  @media (max-width: $mobile-width) {
    min-height: 35px;
  }
}

.logo a {
  display: flex;
  padding: 0;
  margin: 0;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.logo__icon {
  width: 55px;
  height: 55px;

  @media (max-width: $mobile-width) {
    width: 35px;
    height: 35px;
  }
}

.logo__text {
  width: 145px;
  height: 45px;
  padding-top: 3px;
  margin-left: 15px;

  @media (max-width: $mobile-width) {
    display: none;
  }
}

.logo svg {
  width: 100%;
  height: 100%;
}
